var CACHE_NAME = "wd-resize-cache-v1";
var URL_TO_CACHE =
  "https://scp-sandbox-3.wikidot.com/common--javascript/resize-iframe.html";

// Install event - Cache the URL initially
self.addEventListener("install", function (event) {
  console.log("GETTING READY");
  event.waitUntil(
    caches.open(CACHE_NAME).then(function (cache) {
      return fetch(URL_TO_CACHE).then(function (response) {
        console.log("INITIAL CACHE DONE");
        return cache.put(URL_TO_CACHE, response.clone());
      });
    })
  );
});

// Fetch event - Intercept requests to the URL and serve from cache
self.addEventListener("fetch", function (event) {
  var url = new URL(event.request.url);

  if (url.origin + url.pathname === URL_TO_CACHE) {
    event.respondWith(
      caches.open(CACHE_NAME).then(function (cache) {
        return cache.match(URL_TO_CACHE).then(function (cachedResponse) {
          if (cachedResponse) {
            console.log("CACHE HIT");
            return cachedResponse;
          }

          return fetch(event.request).then(function (response) {
            console.log("CACHE MISS");
            cache.put(URL_TO_CACHE, response.clone());
            return response;
          });
        });
      })
    );
  }
});
